import React from 'react';
import { BrowserRouter as Router, Route, Routes, Navigate } from 'react-router-dom';
import Dashboard from './Dashboard';
import Sidebar from './Sidebar';
import Navbar from './Navbar';
import Login from '../pages/Login';

const App = () => {
  return (
    <Router>
      <div>
        {/* Navbar and Sidebar can be placed outside the Routes */}
        <Navbar />
        <Sidebar />
        
        {/* Define routes using Routes and Route */}
        <Routes>
          {/* Default route to redirect to /login */}
          <Route path="/" element={<Navigate to="/login" replace />} />
          <Route path="/login" element={<Login />} />
          <PrivateRoute path="/dashboard" element={<Dashboard />} />
          {/* You can add more routes here */}
        </Routes>
      </div>
    </Router>
  );
};

// PrivateRoute component for protecting dashboard route
const PrivateRoute = ({ element, ...rest }) => {
  const isAuthenticated = fakeAuth.isAuthenticated; // Replace with your authentication logic
  return isAuthenticated ? (
    <div>
      <Navbar />
      <Sidebar />
      {element}
    </div>
  ) : (
    <Navigate to="/login" replace />
  );
};

// Fake authentication
const fakeAuth = {
  isAuthenticated: false, // Set to true when authenticated
};

export default App;
